#navBar {
  background-color: var(--accent);
  position: fixed;
  top: 25px;
  left: 25px;
  z-index: 10;
  border-radius: 50px;
  padding: 0 25px;
}

#navBar a {
  float: left;
  display: block;
  color: var(--white);
  text-align: center;
  font-size: 15px;
  padding: 15px;
  text-decoration: none;
}

#navBar a:hover {
  background-color: var(--accent-highlight);
}

#contactBar {
  background-color: var(--accent);
  display: flex;
  flex-direction: column;
  position: fixed;
  bottom: 25px;
  right: 25px;
  z-index: 10;
  border-radius: 50px;
  padding: 15px;
}

#contactBar a {
  color: var(--white);
  font-size: 30px;
  user-select: none;
  cursor: pointer;
  margin: 5px 0;
}

.app-section {
  padding-top: 100px;
  display: flex;
  flex-direction: column;
}

#home {
  position: relative;
  height: 100vh;
  padding-top: 0;
}

.home-left {
  position: absolute;
  top: calc(50vh - 150px);
  width: 60vw;
}

#headshot {
  position: absolute;
  left: 20vw;
  height: 300px;
  width: 300px;
  border-radius: 10px;
  z-index: 2;
  box-shadow: 1px 1px 10px var(--black);
}

#headshot-block {
  position: absolute;
  left: calc(20vw + 25px);
  top: 25px;
  border-radius: 10px;
  background-color: var(--accent);
  z-index: 1;
  height: 300px;
  width: 300px;
}

.home-right {
  position: absolute;
  height: 100vh;
  left: calc(20vw + 300px + 25px + 25px);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#title-name {
  font-size: 50px;
  font-weight: bold;
  text-align: left;
}

#title-sub {
  font-size: 20px;
  color: var(--accent);
  text-align: left;
}

.header {
  font-size: 30px;
  text-align: right;
  position: absolute;
  right: 65vw;
}

.content {
  font-size: 15px;
  text-align: left;
  position: relative;
  top: -23px;
  left: 40vw;
  padding-top: 30px;
  width: 40vw;
}

.content-title {
  font-size: 20px;
  text-align: justify;
}

.content-sub {
  color: var(--accent);
  font-size: 15px;
  font-style: italic;
  margin-top: 8px;
  position: relative;
}

.content-date {
  color: var(--accent);
  font-size: 15px;
  font-style: italic;
  position: absolute;
  right: 0;
  top: 0;
  padding-top: 62px;
}

.skill-row {
  display: flex;
  flex-direction: row;
  width: 40vw;
}

.proj {
  text-align: left;
  margin-bottom: 50px;
  margin-right: 50px;
  position: relative;
  left: 40vw;
  /* width: calc(100vw - 35vw); */
  padding: 20px 0;
  width: 35vw;
}

.bg-block {
  position: absolute;
  background-color: var(--white);
  top: 3vw;
  height: 33vw;
  width: 35vw;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  z-index: -1;
}

.project-pics {
  width: 32vw;
  position: relative;
  margin-left: calc((35vw - 32vw) / 2);
  border-radius: 10px;
  box-shadow: 1px 1px 10px var(--black);
  z-index: 2;
  background-color: var(--accent);
}

.project-block {
  position: relative;
  padding: 15px 3vw 15px 3vw;
  background-color: var(--white);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.project-tags {
  display: flex;
  flex-direction: row;
}

.tag {
  margin: 0 3px;
  padding: 5px;
  border-style: solid;
  border-radius: 7px;
  border-color: var(--accent);
  color: var(--accent);
  font-size: 15px;
}

.project-title {
  margin-top: 8px;
  color: var(--black);
  font-weight: bold;
  font-size: 20px;
}

.project-desc {
  margin-top: 8px;
  color: var(--black);
  font-size: 15px;
}

.project-links {
  margin-top: 15px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.project-links .button {
  color: var(--white);
  text-decoration: none;
  background-color: var(--accent);
  padding: 10px;
  border-radius: 20px;
  margin: 0 5px;
}

.project-links .button:hover {
  background-color: var(--accent-highlight);
}

.project-links .github {
  position: absolute;
  bottom: 5px;
  right: 20px;
  color: var(--accent);
  font-size: 40px;
}

.project-links .github:hover {
  color: var(--accent-highlight);
}